import React, { Suspense, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { DTX_GRAPHQL_URL } from "../../../../config";
import Button from "../../Button/Button";
import "./BomGet.css";
import {
  GetAllBomListByProductCodeQuery,
  GetAllProjectBomListQuery,
  GetBomdetailsByBomId,
} from "../../../api/graphQlQuery";
import {
  changeActiveSheetName,
  clearsheet,
  hideColumn,
  setCustomPropertyData,
  writeToExcelforBOM,
} from "../../../constFunctions/excelFunction";
import { HIDE_COLUMNS, JWT_ERROR_MESSAGE } from "../../../constFunctions/constants";
import { setNextPage } from "../../../redux-toolkit/slice/TaskpaneSlice";
import { logout } from "../../../constFunctions/copmponentFunctions";
import { runQuery } from "../../../api/apiCalls";
import { setUploadLoader } from "../../../redux-toolkit/slice/action";
import DropDown from "../../DropDown/DropDown";
import ProjectIndicator from "../../ProjectIndicator/ProjectIndicator";
const AddNewMaterail = React.lazy(() => import("../AddNewMaterail/AddNewMaterail"));

function BomGet() {
  const dispatch = useDispatch();
  const { bomMappingData, activeProject } = useSelector((state) => state.taskpane);
  const { activeSheetId } = useSelector((state) => state.action);
  const [productCodes, setProductCodes] = useState([]);
  const [selectedProdCode, setSelectedProdCode] = useState();
  const [allBomdetails, setAllBomDetails] = useState([]);
  const [selectedBom, setSelectdBom] = useState();
  const [coreMaterialsBom, setCoreMaterailBom] = useState([]);
  const [addNewBomMaterial, setAddNewBomMaterial] = useState(false);
  const [prodCodeLoading, setProdCodeLoading] = useState(false);
  const [bomLoading, setBomLoading] = useState(false);
console.log(bomMappingData,"bomMappingData");
  useEffect(() => {
    if (activeProject?.id === "Master") {
      setProdCodeLoading(true);
      try {
        const body = {
          operationName: "MyQuery",
          query: "query MyQuery {\n  product_code {\n    id\n    product_code\n  }\n}\n",
          variables: null,
        };
        runQuery(DTX_GRAPHQL_URL, localStorage.getItem("tenantToken"), body)
          .then((data) => {
            if (data.errors) {
              data.errors.map((element) => {
                if (element?.message.includes(JWT_ERROR_MESSAGE)) {
                  logout();
                  dispatch(setNextPage("homeLogin"));
                }
              });
            } else {
              for (let i = 0; i < data?.data?.product_code?.length; i++) {
                setProductCodes((preState) => [
                  ...preState,
                  { label: data?.data?.product_code[i]?.product_code, value: data?.data?.product_code[i]?.id },
                ]);
              }
            }
          })
          .catch((error) => {
            console.log(error.message, "error.message");
          });
      } catch (error) {
        console.log(error);
      } finally {
        setProdCodeLoading(false);
      }
    } else {
      try {
        const body = {
          operationName: "GetAllProjectBomListQuery",
          query: GetAllProjectBomListQuery,
          variables: { projectId: activeProject?.id },
        };
        runQuery(DTX_GRAPHQL_URL, localStorage.getItem("projectExchangeToken"), body)
          .then((data) => {
            if (data.errors) {
              data.errors.map((element) => {
                if (element?.message.includes(JWT_ERROR_MESSAGE)) {
                  logout();
                  dispatch(setNextPage("homeLogin"));
                }
              });
            } else {
              for (let i = 0; i < data?.data?.core_bom?.length; i++) {
                setAllBomDetails((prevState) => [
                  ...prevState,
                  { label: data?.data?.core_bom[i]?.name, value: data?.data?.core_bom[i]?.id },
                ]);
              }
            }
          })
          .catch((error) => {
            console.log(error.message, "error.message");
          });
      } catch (error) {
        console.log(error);
      } finally {
      }
    }
  }, [true]);
  useEffect(() => {
    document.getElementById("mappingWarning").style.display = "none";
  }, []);
  // when product code selection changes this fuction fecth bom associated with given selelctd producted code
  const handleProdCodeChange = (selectedProdCode) => {
    // for loader in drop down
    setBomLoading(true);
    setAllBomDetails([]);
    setSelectdBom(null);
    setSelectedProdCode(selectedProdCode);
    const token = localStorage.getItem("tenantToken");
    try {
      const body = {
        operationName: "GetAllBomListByProductCodeQuery",
        query: GetAllBomListByProductCodeQuery,
        variables: { product_code: parseInt(selectedProdCode?.value) },
      };
      fetch(DTX_GRAPHQL_URL, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
        body: JSON.stringify(body),
      })
        .then((response) => response.json())
        .then((data) => {
          if (data.errors) {
            data.errors.map((element) => {
              if (element?.message.includes(JWT_ERROR_MESSAGE)) {
                logout();
                dispatch(setNextPage("homeLogin"));
              }
            });
          } else {
            for (let i = 0; i < data?.data?.core_bom?.length; i++) {
              setAllBomDetails((prevState) => [
                ...prevState,
                { label: data?.data?.core_bom[i]?.name, value: data?.data?.core_bom[i]?.id },
              ]);
            }
          }
        })
        .catch((error) => {
          console.log(error);
        });
    } catch (error) {
      console.log(error);
    } finally {
      setBomLoading(false);
    }
  };

  const getSelectedBomDetails = (selectedBom) => {
    setSelectdBom(selectedBom);
  };
  // get fulll details of selected bom
  const importBomMaterial = async () => {
    // checks mandatory fields are there or not
    if (
      bomMappingData.quantity &&
      bomMappingData.total_price &&
      bomMappingData.unit_size &&
      bomMappingData.id &&
      bomMappingData?.custom_material_id &&
      bomMappingData?.associated_product_code_bom
    ) {
      dispatch(setUploadLoader(true));
      const token = localStorage.getItem("tenantToken");
      try {
        const body = {
          operationName: "GetBomdetailsByBomId",
          query: GetBomdetailsByBomId,
          variables: {
            bom_id: selectedBom?.value,
            active: true,
          },
        };
        runQuery(DTX_GRAPHQL_URL, token, body)
          .then(async (data) => {
            if (data?.errors) {
              data.errors.map((element) => {
                if (element?.message.includes(JWT_ERROR_MESSAGE)) {
                  logout();
                  dispatch(setNextPage("homeLogin"));
                  dispatch(setUploadLoader(false));
                }
              });
            } else {
              setCoreMaterailBom(data?.data?.bom_items);
              // to get currently active sheet name
              const sheetname = await changeActiveSheetName(
                selectedBom?.label,
                data?.data?.bom_items[0]?.bom_version_id
              );
              clearsheet(sheetname); // to clear existing data from given sheet

              setCustomPropertyData(activeSheetId, {
                bomVersionId: data?.data?.bom_items[0]?.bom_version_id,
                productCodeId: selectedProdCode?.value,
                productCodeLabel: selectedProdCode?.label,
                bomId: selectedBom?.value,
                bomLabel: selectedBom?.label,
              });
              Object.entries(bomMappingData).map(([key, value], index) => {
                writeHeadings(index, key);
              });
              for (let i = 0; i < data?.data?.bom_items?.length; i++) {
                writeToExcelforBOM(sheetname, i, data?.data?.bom_items[i], bomMappingData);
              }
              // to hide unwanted columns
              HIDE_COLUMNS?.forEach(async (element) => {
                if (bomMappingData[element]) {
                  await hideColumn(bomMappingData[element].address);
                }
              });
              dispatch(setUploadLoader(false));
            }
          })
          .catch((error) => {
            console.log("someThing went wrong");
            dispatch(setUploadLoader(false));
          });
      } catch (error) {
        console.log(error);
        dispatch(setUploadLoader(false));
      } finally {
        dispatch(setUploadLoader(false));
      }
    } else {
      document.getElementById("mappingWarning").style.display = "block";
    }
  };
  // write the headins of given imported data of bom
  const writeHeadings = async (i, key) => {
    await Excel.run((context) => {
      const sheet = context.workbook.worksheets.getActiveWorksheet().getRange(String(bomMappingData[key]?.address + 1));
      sheet.values = [[bomMappingData[key]?.name]];
      sheet.format.fill.color = "#FFC467";
      sheet.format.autofitColumns();
      return context.sync();
    });
  };
  return (
    <>
      <div className=" bom-import-container">
        <ProjectIndicator />
        {activeProject?.id === "Master" && (
          <div>
            <DropDown
              options={productCodes}
              handleChange={handleProdCodeChange}
              value={selectedProdCode}
              label={"Selelct the Product Code"}
              noOptionMessage={"No Product Codes availble"}
              prodCodeLoading={prodCodeLoading}
            />
          </div>
        )}

        {
          <div>
            <DropDown
              options={allBomdetails}
              handleChange={getSelectedBomDetails}
              value={selectedBom}
              label={"Selelct the Bill of Material"}
              noOptionMessage={"No  Bom data availble"}
              isLoading={bomLoading}
            />
          </div>
        }
        <div className="bom-get-btn-container">
          <span className="bom-import-button" onClick={selectedBom && importBomMaterial}>
            <Button label={"Import"} />
          </span>

          <span className="bom-addnewmaterial" onClick={() => setAddNewBomMaterial(true)}>
            <Button label={"Add Material"} />
          </span>
        </div>
        {addNewBomMaterial && (
          <Suspense fallback={<div></div>}>
            {" "}
            <AddNewMaterail setAddNewBomMaterial={setAddNewBomMaterial} />
          </Suspense>
        )}

        <ul className="mappingWarning" id="mappingWarning">
          {!bomMappingData?.id && <li>ID</li>}
          {!bomMappingData?.unit_size && <li>Unit Size (BOM)</li>}
          {!bomMappingData?.quantity && <li>Quantity (BOM)</li>}
          {!bomMappingData?.total_price && <li>Total Price (BOM)</li>}
          {!bomMappingData?.custom_material_id && <li>Material Id</li>}
          {!bomMappingData?.associated_product_code_bom && <li>Associated Product code (BOM)</li>}
          <li>you have to map given mandatory field(s)</li>
        </ul>
      </div>
    </>
  );
}

export default BomGet;
